<template>
    <div>
        <Table :column="columns" :data="datas"></Table>
    </div>
</template>

<script>
    export default {
        name: 'enterprise',
        data() {
            return {
                columns: [{
                    title: 'ID',
                    key: 'id',
                    align: 'center',
                }, {
                    title: '描述',
                    key: 'desc',
                    align: 'center',
                }, {
                    title: '排序（越小优先级越高）',
                    key: 'sort',
                    align: 'center',
                }],
                datas: []
            }
        }
    }
</script>

<style lang="scss" scoped>
</style>


